.input {
  width: 372px;
  height: 36px;
  border-radius: 5px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  padding: 8px 12px;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
  gap: 48px;
}

.superInput {
  border: 1px solid #D1D1D1;
}

.errorInput {
  border: 1px solid #CC0000;
}

