.message {
  height: 77px;
  width: 575px;
  margin: 0 0 47px auto;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
  gap: 12px;
}

.imageAndText img {
  width: 48px;
  height: 48px;
}

.text {
  background-color: #0066CC;
  padding: 7px 13px 8px 13px;
  box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
  color: #fff;
  font-family: 'Montesserat', sans-serif;
  position: relative;
  z-index: 1;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.text:after {
  content: '';
  position: absolute;
  z-index: 5;
  right: -12px;
  bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 0 0 12px;
  border-color: transparent transparent transparent #0066CC;
  box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
}

.name {
  text-align: right;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 3px;
}

.messageText {
  text-align: right;
  font-family: "Montesserat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}

.time {
  text-align: right;
  margin-right: 5px;
  margin-top: 5px;
  color: var(--color-text)
}